* {
  margin: 0;
  text-decoration: none;
  list-style: none;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.b7336e49.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

body {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  vertical-align: middle;
}

.menu__list {
  align-items: center;
  padding: 0;
  display: flex;
}

.menu__link {
  color: #000;
  align-items: center;
  height: 60px;
  display: flex;
}

.menu__list .menu__item:nth-child(4) .menu__link:hover {
  color: #00acdc;
}

.menu__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
}

.menu__item:first-child {
  margin-left: 0;
}

.menu__item:last-child {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
